<template>
  <div class="hello">
    <h1>svg测试hqa</h1>
    <div id="boxContainer" style="position: relative;width: 300px;height: 300px;">
      <!--<svg id="box" width="300" height="300">-->
      <!--&lt;!&ndash;<polyline points='220,10 300,210 170,250 123,234' fill='none' stroke='#000' stroke-width='1'></polyline>&ndash;&gt;-->
      <!--<text x="250" y="250" fill="red" text-anchor="start" dominant-baseline="hanging">I study SVG</text>-->

      <!--</svg>-->
      <div id="drawing" style="width:250px;height: 250px;background: url('../../static/33.jpeg') center/cover"></div>
      <!--<img src="../../static/33.jpeg" style="width: 500px;height: 500px;position: absolute;top: 0;z-index: -1;display: block;left: 0;" alt="">-->
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import svg from 'svg.js'
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        resArr: [],
        arr1: [],
        arr2: []
      }
    },
    methods: {
      svg_data() {
        var draw = SVG('drawing').size(250, 250)
        $.ajax({
            type: 'post',
            url: 'http://localhost:3000/users/svgpost',
            success: function (data) {
              console.log(JSON.parse(data[0].obj))
              function dataEdit(shapeData) {
                var data_middle = [];
                var middleArr = [];
                var middleArr_inside = [];
                console.log(shapeData)
                shapeData.map(function(ele){
                  middleArr = [];
                  ele.arr.split(' ').map(function (ele1) {
                    middleArr_inside = [];
                    ele1.split(',').map(function (ele2) {
                      ele2 = ele2 / 2
                      middleArr_inside.push(ele2)
                    })
                    middleArr.push(middleArr_inside)
                  })
                  data_middle.push(middleArr)
                })
                //对转换后的数组进行数据处理
                data_middle = data_middle.map(function (ele) {
                  return ele.map(function (ele1) {
                    return ele1.join(',')
                  })
                })
                return data_middle = data_middle.map(function (ele,i) {
                  var obj = {}
                  obj.arr = ele.join(' ');
                  obj.houseMark = shapeData[i].houseType;
                  return obj
                })
              }
              //多边形
              dataEdit(JSON.parse(data[0].obj).line).forEach(function (ele, i) {
                var a = draw.polygon(ele.arr).fill('rgba(255,254,218,.2)').stroke({
                  width: 1,
                  color:'#FF4E7B'
                })
                $(a.node).attr({'houseMark':ele.houseMark})
              })

              //矩形
              dataEdit(JSON.parse(data[0].obj).rect).forEach(function (ele, i) {
                console.log(ele)
                var a = draw.path('M'+ele.arr).fill('rgba(255,254,218,.2)').stroke({
                  width: 1,
                  color:'#FF4E7B'
                })
                $(a.node).attr({'houseMark':ele.houseMark})
              })
              $('#SvgjsSvg1001').children().click(function () {
                // alert(0)
                alert($(this).attr('houseMark'))
              })
            }
        })
      }
    },
    mounted() {
      var that = this
      this.svg_data()


    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  * {
    margin: 0;
    padding: 0
  }

  svg {
    border: 1px seagreen solid;
    position: relative;
  }

  ul li:hover {
    color: blue
  }
</style>



